/* @import url("http://fonts.cdnfonts.com/css/billabong"); */
html {
  font-size: 10px;
  font-family: sans-serif;
}

p {
  font-size: 1.6rem;
  line-height: 1.5;
}

h1 {
  font-family: "billabong", sans-serif;
  text-align: center;
  /* font-size: 13rem; */
  font-weight: 100;
  margin: 2rem;
  letter-spacing: -1px;
  color: black;
  text-decoration: none;
}
@media screen and (min-width: 601px) {
  h1 {
    font-size: 13rem;
  }
  .form {
    width: 360px;
  }
  .figure {
    flex-basis: calc(33.3% - 4rem);
  }
  hr {
    width: 360px;
  }
}

@media screen and (max-width: 600px) {
  h1 {
    font-size: 8rem;
  }
  .form {
    width: 200px;
  }
  .figure {
    flex-basis: calc(60%);
  }
  hr {
    width: 200px;
  }
}

@font-face {
  font-family: "billabong", sans-serif;
  /* src: url("https://fonts.cdnfonts.com/css/billabong") format("svg"); */
  font-weight: normal;
  font-style: normal;
}

h1 a {
  color: black;
  text-decoration: none;
}

h1 a:focus {
  outline: 0;
}

body {
  background: offwhite;
}
.photoGrid {
  display: flex;
  flex-wrap: wrap;
  max-width: 1000px;
  margin: 0 auto;
}
.figure {
  /* border: 1px solid #d3d3d3; */
  border: 1px solid #065784;
  padding: 2rem;
  flex-grow: 1;
  margin: 0 2rem 2rem 2rem;
  border-radius: 0.5rem;
}
.figure .single-photo {
  flex-basis: calc(33.333% - 4rem);
  flex-grow: 1;
  flex-shrink: 0;
  margin: 0 2rem 2rem 2rem;
  padding: 2rem;
  border: 1px solid #d3d3d3;
  background: #fff;
  position: relative;
}
.single-photo {
  max-width: 1000px;
  margin: 0 auto;
  display: flex;
  background: #fff;
  border: black;
}
.single-photo .figure {
  box-shadow: none;
  margin: 0 2rem 0 0;
  border: 0;
  padding: 0;
  flex: 1 0 60%;
  max-width: 60%;
}
.single-photo .comments {
  flex: 1 0 40%;
  max-width: 40%;
}
.single-photo .photo {
  width: 100%;
  margin: 0;
  object-fit: cover;
}
.photo {
  width: calc(100% + 4rem);
  margin-left: -2rem;
  margin-top: -2rem;
  max-width: none;
  border-radius: 0.5rem 0.5rem 0 0;
}
.comment {
  width: 100%;
}
.comment p {
  border-bottom: 1px solid #d3d3d3;
  padding: 0.5rem 0;
}
.comment-form input,
.comment-form textarea {
  width: 100%;
  border: 0;
  font-size: 1.3rem;
  padding: 1rem 0;
  border-bottom: 1px solid #d3d3d3;
  outline: none;
  resize: vertical;
}
.addIcon {
  display: table;
  margin: 20px auto;
  border: none;
}
.button-container {
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}
button,
.button {
  border: 2px solid #f2f2f2;
  background: none;
  flex-basis: 48%;
  display: inline-block;
  line-height: 2;
  text-decoration: none;
  padding: 5px;
  text-align: center;
  font-size: 15px;
  color: red;
  transition: all 0.2s;
  box-sizing: padding-box;
}
button:hover,
.button:hover,
button:focus,
.button:focus {
  border-color: #00f;
  outline: 0;
}
.speech-bubble {
  background: url("https://d30y9cdsu7xlg0.cloudfront.net/png/51904-200.png")
    center no-repeat;
  background-size: contain;
  padding: 10px;
  margin: 5px;
  display: inline-block;
  border: none;
  position: relative;
}
.comment-count {
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  color: black;
}
.form {
  padding: 0 0 0;
  margin: auto;
}

.form input {
  font-family: sans-serif;
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
}
.form button {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: #065784;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #ffffff;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
}
.form button:hover,
.form button:active,
.form button:focus {
  background: #7b8589;
}
.loader {
  height: 20%;
  width: 100%;
  display: flex;
  position: fixed;
  align-items: center;
  justify-content: center;
  font-size: 28pt;
}

a {
  color: black;
  text-decoration: none;
}

.message {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 2rem;
  margin: 20px auto;
  text-align: left;
}

hr {
  color: #065784;
  background-color: black;
  font-weight: bolder;
  height: 0.5rem;

  border-radius: 0.5rem;
  margin: 2rem auto;
}
